.commentary-content-wrapper {
  display: flex;
  justify-content: space-between;
  gap: 80px;
  margin-bottom: 60px;
  align-items: flex-start;
  h3 {
    margin-bottom: 80px;
  }
}
.commentary-top-content {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.commentary-headings-wrapper {
  width: 50%;
}
.commentary-content-left {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 50%;
  gap: 20px;
}
.learn-libert-img {
  width: 50%;
  object-fit: contain;
}
.commentary-img-wrapper {
  width: 20%;
  text-align: end;
  img {
    width: 80%;
  }
}
.views-wrapper {
  display: flex;
  align-items: center;
  gap: 20px;
}
.commentary-views-img-wrapper {
  width: 30%;

  img {
    width: 100%;
  }
}
.commentary-content-wrapper .commentary-wrapper {
  width: 50%;
  padding-bottom: 40px;
}

.commentary-content-wrapper .views-wrapper {
  width: fit-content;
  background-color: #fff;
  padding: 20px 40px;
  border-radius: 20px;
  border-radius: 20px;
  div {
    p:first-child {
      font-size: 80px;
      font-family: "TT-Hoves-Pro";
      font-weight: 600;
    }
  }
}
@media screen and (max-width: 1200px) {
  .learn-libert-img {
    width: 40%;
  }
  .commentary-content-left {
    width: 60%;
  }
}
@media screen and (max-width: 800px) {
  .learn-libert-img {
    width: 35%;
  }
}
@media screen and (max-width: 768px) {
  .commentary-content-wrapper {
    flex-wrap: wrap;
    justify-content: center;
  }
  .learn-libert-img {
    width: 50%;
  }
  .commentary-content-left {
    width: 100%;
  }
}
