.letter-from-ceo {
  .container {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    gap: 40px;

    .left-wrapper {
      width: 50%;
      p {
        margin: 30px 0;
      }
      a {
        margin-top: 20px;
      }
      .expandable-text {
        overflow: hidden;
        max-height: 0;
        transition: max-height 0.5s ease;
      }

      .expandable-text.expanded {
        max-height: 200px;
      }

      .expandable-text p {
        margin: 0;
        padding: 0;
      }
    }
    .right-wrapper {
      width: 50%;
      padding-top: 40px;
      display: flex;
      flex-direction: column;
      text-align: center;
      align-items: center;
    }
    .image-wrapper {
      width: 50%;
    }
    .image-wrapper img:last-child {
      width: 80%;
    }
  }
}


