section.testimonials {
  padding: 4% 0;
  .testimonial-icon {
    font-size: 100px;
  }
}
.testimonials .container {
  display: flex;
  align-items: center;
  justify-content: space-between;

  h2:first-child {
    margin: 0;
  }
  h2:last-child {
    margin-top: 40px;
    margin-bottom: 0;
  }
}
.testimonials-header-wrapper {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.testimonials-media-hits-wrapper {
  display: flex;
  justify-content: flex-end;
  div {
    width: 100%;
    background-color: #f1f1f1;
    padding: 20px 40px;
    display: flex;
    flex-direction: column;
    gap: 10px;

    p:first-child {
      font-size: 5rem;
      font-family: "TT-Hoves-Pro";
      font-weight: 600;
    }
    img {
      margin-top: auto;
      width: 70px;
    }
  }
}
