.global-presence {
  background-image: url("../../assets/images/globe-bg.png");
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  display: flex;
  flex-direction: column;
  align-items: center;

  .btn {
    margin-top: 80px;
    text-transform: uppercase;
    font-weight: 700;
  }
}
.global-presence .tabs-wrapper {
  display: flex;
  gap: 40px;
}
.global-presence .tabs span {
  font-family: "Inter", serif;
  font-size: 18px;
  font-weight: 400;
  text-transform: uppercase;
}
.globe-info {
  display: none;
}
.global-presence .tabs .info {
  margin-top: 50px;
  background-color: #fff;
  padding: 20px 40px;
  margin-left: 15px;
  width: 90%;
  border-radius: 20px;
  display: flex;
  flex-direction: column;
  gap: 5px;
  span {
    font-family: "TT-Hoves-Regular";
    font-size: 5rem;
    font-weight: 700;
  }
  span.icomoon {
    font-weight: normal;
    font-size: 80px;
  }
  img {
    margin-top: 20px;
    width: 80px;
  }
  p {
    display: block;
  }
}
.global-presence .tabs li p {
  display: none;
}
.global-presence .tabs li.active p {
  display: block;
}
.global-presence .tabs li.active span {
  font-weight: 700;
}
.global-presence .tabs {
  width: 25%;
  li {
    background-color: #e1e1e1;
    padding: 20px;
    border-radius: 25px;
    cursor: pointer;
    margin-bottom: 10px;
    &.active {
      background-color: #fff;
    }
  }
}

.global-presence .tab-content {
  width: 75%;
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 20px;
}

.global-presence .tab-card {
  display: flex;
  flex-direction: column;
  gap: 20px;
  padding: 20px;
  background-color: #fff;
  &:first-child {
    grid-column: 1 / 3;
    flex-direction: row;
    .tab-card-img-wrapper {
      width: 50%;
      height: 260px;
    }
    .tab-card-content-wrapper {
      width: 50%;
    }
  }
  span {
    font-family: "Inter", serif;
    font-size: 18px;
    font-weight: 700;
    text-transform: uppercase;
  }
}

.tab-card-content-wrapper {
  padding: 20px;
  background-color: #fff;
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.tab-card-img-wrapper {
  height: 260px;
  border-radius: 20px;
  overflow: hidden;
  img {
    transform: scale(1.5);
  }
}

@media screen and (max-width: 1300px) {
  .global-presence .tabs > div {
    margin-left: 0;
    width: 100%;
  }
}
@media screen and (max-width: 768px) {
  .global-presence .tabs-wrapper {
    display: flex;
    flex-wrap: wrap;
  }
  .global-presence .tabs {
    width: 100%;

    display: flex;
    align-items: center;
    flex-wrap: wrap;
    gap: 20px;
  }
  .global-presence .tab-content {
    width: 100%;
  }
  .global-presence .tabs .info {
    display: none;
  }
  .globe-info {
    margin: 40px auto;
    background-color: #fff;
    padding: 20px 40px;

    width: 80%;
    border-radius: 20px;
    display: flex;
    flex-direction: column;
    gap: 5px;
    span {
      font-family: "TT-Hoves-Regular";
      font-size: 5rem;
      font-weight: 700;
    }
    span.icomoon {
      font-weight: normal;
      font-size: 80px;
    }
    img {
      margin-top: 20px;
      width: 80px;
    }
    p {
      display: block;
    }
  }
}
