.btn {
  font-family: "Inter", serif;
  font-size: 24px;
  font-weight: 500;
  line-height: initial;
  text-align: center;
  color: #1e1e1e;
  background-color: #f9c70f;
  border: 1px solid #1e1e1e;
  padding: 12px 50px;
  border-radius: 30px;
  transition: 0.3s all;
}
.btn:hover {
  background-color: #1e1e1e;
  color: #f9c70f;
}
.donate-btn {
  font-weight: 700;
  text-transform: uppercase;
}
@media screen and (max-width: 1300px) {
  .btn {
    font-size: 18px;
  }
}
