.mentions a {
  width: 100%;
  text-align: center;
}
.mentions-content-wrapper {
  display: flex;
  justify-content: space-between;
  gap: 80px;
  margin-bottom: 40px;

  h3 {
    margin: 0 0 60px 0;
  }
}

.mentions-content-wrapper .mentions-wrapper {
  width: 75%;
  display: flex;
  flex-direction: column;

  h2:last-child {
    margin-top: auto;
    margin-bottom: 0;
  }
}

.mentions-content-wrapper .media-hits-wrapper {
  width: 25%;
  display: flex;
  justify-content: flex-end;
  div {
    width: 100%;
    background-color: #f1f1f1;
    padding: 20px 40px;
    display: flex;
    flex-direction: column;
    gap: 10px;

    p:first-child {
      font-size: 5rem;
      font-family: "TT-Hoves-Pro";
      font-weight: 600;
    }
    img {
      margin-top: auto;
      width: 70px;
    }
  }
}

.media-hit {
  padding: 40px 30px 20px 30px;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  h4 {
    font-size: 1.75rem;
  }
  p {
    width: 100%;
    text-transform: uppercase;
    &:last-child {
      width: fit-content;
    }
  }
}
.media-hit .media-views {
  width: fit-content;
  padding: 10px 15px 10px 10px;
  border: 1px solid #9a8291;
  border-radius: 20px;

  svg {
    width: 15px;
    margin-right: 5px;
    margin-left: 5px;
  }
}

.mentions-media-hits {
  height: 550px;
  overflow: hidden;
  position: relative;
}
.mentions-media-hits.expanded {
  height: auto;
}
.mentions-media-hits::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(
    to bottom,
    rgba(255, 255, 255, 0) 200px,
    rgba(255, 255, 255, 1) 500px
  );
  pointer-events: none;
  z-index: 1;
}
.mentions-media-hits.expanded::after {
  background: none;
}
.mentions .mentions-logos {
  margin-top: 50px;

  h2 {
    margin-left: 40px;
  }
}
.mentions .mentions-logos-wrapper {
  padding: 40px;
  overflow: hidden;
}
.mentions .logo-slider:not(:last-child) {
  margin-bottom: 40px;
}
.mentions .slider-slide {
  margin-right: 20px;
  flex-direction: column;
  gap: 20px;

  img {
    height: 100px;
    width: 150px;
    object-fit: contain;
    -webkit-filter: grayscale(100%);
    filter: grayscale(100%);
  }
}

@media screen and (max-width: 768px) {
  .mentions-content-wrapper .media-hits-wrapper div {
    padding: 10px;
  }
}
