.why-sfl-exist {
  position: relative;
  z-index: 2;
  text-align: center;
  padding-bottom: 15.5%;
}
.why-sfl-exist__wrapper {
  max-width: 1050px;
}
.why-sfl-exist .container {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.why-sfl-exist__problem {
  text-align: start;
}
.why-sfl-exist__problem h4 {
  margin-bottom: 20px;
}
.why-sfl-exist__problem > div {
  position: relative;
  background-image: url("data:image/svg+xml,%3csvg width='100%25' height='100%25' xmlns='http://www.w3.org/2000/svg'%3e%3crect width='100%25' height='100%25' fill='none' rx='20' ry='20' stroke='black' stroke-width='4' stroke-dasharray='2%2c 8' stroke-dashoffset='0' stroke-linecap='butt'/%3e%3c/svg%3e");
  padding: 40px;
}
.why-sfl-exist__solution {
  margin-top: 25px;
  text-align: start;
}
.why-sfl-exist__solution .solution-cards-wrapper {
  display: flex;
  gap: 30px;
  justify-content: center;
}
.why-sfl-exist__solution .solution-card {
  background-color: #fff;
  padding: 40px;
  width: 33%;
}
.why-sfl-exist__solution .solution-card h4 {
  margin-bottom: 20px;
}
.why-sfl-exist__solution .solution-card span {
  font-size: 138px;
  font-family: "TT-Hoves-Regular";
  font-weight: 400;
}
.why-sfl-exist__solution .solution-card:nth-child(1) span {
  color: var(--peach);
}
.why-sfl-exist__solution .solution-card:nth-child(2) span {
  color: var(--pink);
}
.why-sfl-exist__solution .solution-card:nth-child(3) span {
  color: var(--aqua);
}
.why-sfl-exist__solution .solution-card img {
  width: 60px;
  margin-bottom: 15px;
  margin-left: -25px;
}
.why-sfl-exist__solution .solution-card:nth-child(1) img {
  width: 80px;
}
.why-sfl-exist > img {
  width: 50%;
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translate(-50%, 0%);
}
@media screen and (max-width: 768px) {
  .why-sfl-exist__solution .solution-cards-wrapper {
    flex-wrap: wrap;
  }
  .why-sfl-exist__solution .solution-card {
    width: 100%;
  }
}
