svg#arrow {
  position: absolute;
  top: -100%;
  right: 16%;
  transform: scale(0.8);
}
#mask-rect {
  animation: slide-reveal 2s linear forwards;
}

@keyframes slide-reveal {
  to {
    width: 275px;
  }
}

@media screen and (max-width: 1200px) {
  svg#arrow {
    top: -125%;
    right: 10%;
    transform: scale(0.6);
  }
}
@media screen and (max-width: 1024px) {
  svg#arrow {
    display: none;
  }
}
