* {
  box-sizing: border-box;
}

@media (prefers-reduced-motion: no-preference) {
  :root {
    scroll-behavior: smooth;
  }
}
html {
  font-size: 100%;
}
body {
  margin: 0;
  background: #fff;
  font-family: "Montserrat", sans-serif;
  font-size: 18px;
  line-height: 1.1;
  color: #000;
}

img {
  width: 100%;
  object-fit: cover;
}

h1,
h2,
h4 {
  margin: 0;
}

h1 {
  text-align: center;
  margin: 50px 0;
  font-weight: 400;
  text-transform: none;
  font-size: 5rem;
}

h2 {
  font-family: "Inter", serif;
  letter-spacing: 0.2em;
  font-size: 1.25rem;
  margin-bottom: 30px;
  text-transform: uppercase;
}

h3 {
  font-family: "Montserrat", sans-serif;
  margin: 30px 0;
  font-size: 2.625rem;
  font-weight: 700;
  text-transform: capitalize;
}
h4 {
  font-family: "Montserrat", sans-serif;
  font-size: 1.875rem;
  font-weight: 700;
}

p {
  font-family: "Inter", serif;
  font-size: 1.125rem;
  margin: 0;
  line-height: 1.2em;
}

a {
  font-family: "Inter", serif;
  font-size: 1.125rem;
  font-weight: bold;
  display: inline-block;
  text-decoration: none;
  color: #000;
}

ul,
ol {
  margin: 0;
  padding: 0;
  list-style: none;
}

button {
  cursor: pointer;
  font-size: inherit;
  font-family: inherit;
  line-height: inherit;
  border: none;
  box-shadow: none;
  background: transparent;
  padding: 0;
  color: inherit;
}

input {
  box-shadow: none;
  margin: 0;
  font-size: inherit;
  padding: 0;
}

input[type="search"]::-webkit-search-decoration,
input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-results-button,
input[type="search"]::-webkit-search-results-decoration {
  display: none;
}

label {
  display: inline-block;
}

button {
  border-radius: 0;
}

button:focus:not(:focus-visible) {
  outline: 0;
}

input,
button,
select,
optgroup,
textarea {
  margin: 0;
  font-family: inherit;
  font-size: inherit;
  line-height: inherit;
}

button,
select {
  text-transform: none;
}

[role="button"] {
  cursor: pointer;
}

select {
  word-wrap: normal;
}

select:disabled {
  opacity: 1;
}

[list]:not([type="date"]):not([type="datetime-local"]):not([type="month"]):not(
    [type="week"]
  ):not([type="time"])::-webkit-calendar-picker-indicator {
  display: none !important;
}

button,
[type="button"],
[type="reset"],
[type="submit"] {
  -webkit-appearance: button;
}

button:not(:disabled),
[type="button"]:not(:disabled),
[type="reset"]:not(:disabled),
[type="submit"]:not(:disabled) {
  cursor: pointer;
}

::-moz-focus-inner {
  padding: 0;
  border-style: none;
}

textarea {
  resize: vertical;
}

fieldset {
  min-width: 0;
  padding: 0;
  margin: 0;
  border: 0;
}

::-webkit-datetime-edit-fields-wrapper,
::-webkit-datetime-edit-text,
::-webkit-datetime-edit-minute,
::-webkit-datetime-edit-hour-field,
::-webkit-datetime-edit-day-field,
::-webkit-datetime-edit-month-field,
::-webkit-datetime-edit-year-field {
  padding: 0;
}

::-webkit-inner-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-color-swatch-wrapper {
  padding: 0;
}

::-webkit-file-upload-button {
  font: inherit;
  -webkit-appearance: button;
}

::file-selector-button {
  font: inherit;
  -webkit-appearance: button;
}

output {
  display: inline-block;
}

iframe {
  border: 0;
}

summary {
  display: list-item;
  cursor: pointer;
}

progress {
  vertical-align: baseline;
}

[hidden] {
  display: none !important;
}
@media screen and (max-width: 1440px) {
  html {
    font-size: 95%;
  }
}
@media screen and (max-width: 1200px) {
  html {
    font-size: 85%;
  }
}
@media screen and (max-width: 1024px) {
  html {
    font-size: 75%;
  }
}
@media screen and (max-width: 768px) {
  html {
    font-size: 65%;
  }
}
