.chart-container-two svg {
  transform: scale(0.91);
  margin-right: -25px;
  margin-top: -75px;
}

@media screen and (max-width: 1168px) {
  .chart-container-two svg {
    margin-top: -55px;
  }
}

@media screen and (max-width: 1024px) {
  .chart-container-two svg {
    margin-top: -30px;
  }
}
@media screen and (max-width: 768px) {
  .chart-container-two svg {
    margin-top: -75px;
    transform: scale(0.81);
    margin-right: 0px;
  }
}
