.featured-stories .story-item:not(:last-child) {
  padding-bottom: 80px;
  margin-bottom: 80px;
}
.featured-stories .story-item {
  padding: 60px;
  display: flex;
  justify-content: space-between;
  gap: 40px;
  min-height: 510px;
  background-size: 50%;
  background-repeat: no-repeat;
  .expandable-text {
    overflow: hidden;
    max-height: 0;
  }

  .expandable-text.expanded {
    max-height: fit-content;
  }
  .content-wrapper {
    width: 50%;
    display: flex;
    flex-direction: column;
    h2 {
      text-align: start;
      margin: 0;
    }
    h3 {
      text-align: start;
      margin: 10px 0;
    }
    p {
      margin: 10px 0;
      text-align: start;
    }
    a {
      margin-top: auto;
    }
  }
  .image-wrapper {
    width: 50%;
  }
}
.featured-stories .story-item.global {
  background-color: var(--peach);
  background-image: url("../../assets/images/the-human-heart.png");
  background-position: right 0% top 20px;
}
.featured-stories .story-item.united {
  flex-direction: row-reverse;
  background-color: var(--pink);
  background-image: url("../../assets/images/proving-the-power.png");
  background-position: left -5% top 20px;
}
.featured-stories .story-item.sfl {
  background-color: var(--aqua);
  background-image: url("../../assets/images/feature-has-arrived.png");
  background-position: right -10% top 20px;
}
