.hero {
  /* background: center / cover no-repeat url("/src/assets/images/hero-image.webp"); */
  background-image: url(../../assets/images/hero-image.webp);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: left;
  height: 100vh;
  margin-top: -140px;
  padding-top: 15%;
  .container {
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: center;
    height: 100%;
  }
  p {
    text-transform: uppercase;
    font-size: 26px;
    span {
      font-size: 90px;
    }
  }
}
.hero-heading {
  width: 70%;
  text-align: center;
  margin: 50px 0;
  font-weight: 400;
  text-transform: none;
  background-image: url(../../assets/images/heading-bg.png);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  color: transparent;
  background-clip: text;
  -webkit-background-clip: text;
}
.hero-img {
  width: 20%;
}
@media screen and (max-width: 1300px) {
  .hero-heading {
    font-size: 6vw;
  }
  .hero-img {
    width: 15%;
  }
}
@media screen and (max-width: 1024px) {
  .hero {
    background-position: center;
  }
}
