.footer {
  border-radius: 0 0 35px 35px;
  padding: 3% 0%;
}

.footer .container div {
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-wrap: wrap;
}
.footer .copy-to-clipboard {
  position: relative;
}
.footer .copy-to-clipboard::after {
  content: "";
}
.footer .copy-to-clipboard.clicked::after {
  content: attr(data-message);
  position: absolute;
  font-size: 14px;
  width: 205px;
  top: -40px;
  background-color: #fff;
  padding: 10px;
  border-radius: 5px;
  border: 1px solid #4caf50;
}
.footer-second-wrapper {
  margin-top: 80px;
}

.footer .container h3 {
  width: 33%;
  color: #f9c70f;
}
.footer .container a.sfl-website {
  width: 33%;
}
.footer-images-wrapper {
  gap: 20px;

  img {
    width: 95px;
    height: 95px;
  }
}
.footer .container div.share-report {
  flex-direction: column;
  align-items: flex-start;
  gap: 25px;
  width: 25%;
  p {
    text-transform: uppercase;
    color: #fff;
    font-weight: 200;
    font-size: 24px;
  }
}
.footer-social-icons-wrapper {
  gap: 20px;
  span {
    color: #fff;
    font-size: 40px;
  }
  img {
    width: 36px;
    height: 36px;
  }
}
.footer-end {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 1.5% 10% 2% 10%;
  flex-wrap: wrap;
  gap: 10px;
  a,
  p {
    font-size: 16px;
    font-weight: 400;
  }
  a:hover {
    text-decoration: underline;
  }
}
