.video-slide .video-views {
  width: fit-content;
  padding: 10px 20px 10px 15px;
  border: 1px solid #fff;
  border-radius: 20px;
  color: #fff;
  position: absolute;
  bottom: 20px;
  right: 30px;
  svg {
    width: 15px;
    margin-right: 5px;
    margin-left: 5px;
  }
}
.video-slide h4 {
  margin-top: 30px;
  font-size: 26px;
}
.video-slide .video-slide-content {
  position: relative;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  border-radius: 20px;
  padding: 20px 30px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 250px;

  img {
    transition: 0.3s all;
    width: 130px;
    &:hover {
      transform: scale(1.1);
    }
  }
}
