.single-storie {
  background-image: url("../assets/images/globe-bg.png");
  background-size: cover;
  background-position: center;
  min-height: 100vh;
}
.storie-content-wrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 40px;
  img {
    width: 50%;
  }
  p {
    margin-bottom: 20px;
  }
  span {
    font-family: "Inter", serif;
    font-size: 18px;
    font-weight: 700;
    text-transform: uppercase;
  }
  h4 {
    margin-top: 10px;
    margin-bottom: 40px;
  }
}
