.year-in-number a {
  text-align: center;
  text-transform: uppercase;
  padding: 40px 0 0 0;
}

.year-in-number {
  span.number-one {
    font-size: 5rem;
    font-family: "TT-Hoves-Pro";
    font-weight: 600;
  }
  span.number-two {
    font-size: 2.625rem;
    font-family: "TT-Hoves-Pro";
    font-weight: 600;
  }
  .year-in-number__icon {
    font-size: 4.375rem;
  }
  .year-in-number__impact-icon {
    font-size: 5.313rem;
  }
  .dashboard-wrapper {
    display: flex;
    gap: 20px;
    justify-content: center;
  }

  .large-card {
    background-image: url("../../assets/images/graphic.png");
    background-size: contain;
    background-position: center;
    background-repeat: no-repeat;
    background-color: #f1f1f1;
    width: 66.6%;
    height: 450px;
    padding-left: 130px;
    padding-top: 40px;
    display: flex;
    flex-direction: column;
    gap: 10px;
  }

  .medium-card {
    background-image: url("../../assets/images/total-reach.png");
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    padding: 40px;
    background-color: #f9c70f;
    width: 32.3%;
    height: 450px;
    display: flex;
    flex-direction: column;
    gap: 10px;
  }
  .small-cards-wrapper {
    display: flex;
    padding-top: 20px;
    align-items: center;
    justify-content: center;
    gap: 20px;
  }
  .small-card {
    width: 33%;
    height: 230px;
    padding: 40px;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .small-card span {
    text-align: center;
  }
  .small-card:nth-child(1) {
    background-color: var(--peach);
  }

  .small-card:nth-child(2) {
    background-color: var(--pink);
  }

  .small-card:nth-child(3) {
    background-color: var(--aqua);
  }

  .impact-grid {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 20px;
  }

  .impact-item {
    background-color: #f1f1f1;
    padding: 20px 40px;
    display: flex;
    flex-direction: column;
    gap: 10px;
  }
}
.year-in-number .content-wrapper {
  display: flex;
  flex-direction: column;
}

.year-in-number .numbers-wrapper {
  display: flex;
  justify-content: space-between;
  gap: 40px;
}
.year-in-number .number-info-wrapper {
  background-color: #f1f1f1;
  padding: 40px;
  border-radius: 20px;
  width: 33%;

  p:first-child {
    font-size: 80px;
    margin-bottom: 40px;
  }

  p:last-child {
    font-size: 35px;
  }
}
@media screen and (max-width: 768px) {
  .dashboard-wrapper {
    flex-wrap: wrap;
  }
  .year-in-number .medium-card {
    width: 90%;
  }
  .year-in-number .large-card {
    width: 90%;
  }
  .year-in-number .small-cards-wrapper {
    flex-wrap: wrap;
  }
  .year-in-number .small-card {
    width: 33%;
  }
  .year-in-number .impact-grid {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media screen and (max-width: 550px) {
  .year-in-number .small-card {
    width: 100%;
  }
  .year-in-number .medium-card {
    width: 100%;
  }
  .year-in-number .large-card {
    width: 100%;
  }
}
