.testimonials .swiper {
  padding: 60px 0;
}
.slider-quotes {
  height: auto;
  background-color: #f1f1f1;
  padding: 20px 10px 0px 10px;
}
.quotes-content-wrapper {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  gap: 20px;
  padding-bottom: 90px;
}
.quotes-speaker-info {
  text-align: end;
  padding: 0 30px;
  margin-top: 40px;
  position: relative;
  z-index: 1;
}
.quote-icon {
  width: 100px;
  margin-top: -50px;
  margin-left: 20px;
}
.quotes-descr {
  padding: 0 30px;
  font-weight: 300;
  line-height: 1.3em;
}

.quotes-speaker-thumb {
  position: absolute;
  width: 200px;
  bottom: 0;
}

.testimonials .swiper-button-prev {
  top: 45%;
  left: 4%;
  width: 100px;
  height: 100px;
  border-radius: 50%;
  background: #fff;
  &::after {
    color: #000;
    margin-right: 10px;
  }
}
.testimonials .swiper-button-next {
  top: 45%;
  right: 4%;
  width: 100px;
  height: 100px;
  border-radius: 50%;
  background: #fff;
  &::after {
    color: #000;
    margin-left: 10px;
  }
}
.testimonials .swiper-pagination {
  /*  transform: translate3d(0, 60px, 0) !important; */

  .swiper-pagination-bullet {
    background-color: #f9c70f;
    width: 17px;
    height: 17px;
  }
}
.testimonials .swiper-slide .quotes-content-wrapper {
  visibility: hidden;
}
.testimonials .swiper-slide-prev .quotes-content-wrapper,
.testimonials .swiper-slide-next .quotes-content-wrapper,
.testimonials .swiper-slide-active .quotes-content-wrapper {
  visibility: visible;
}

@media screen and (max-width: 1440px) {
  .quotes-content-wrapper {
    padding-bottom: 70px;
  }
  .testimonials .swiper-button-prev {
    top: 45%;
    left: 2%;
    width: 80px;
    height: 80px;
  }
  .testimonials .swiper-button-next {
    top: 45%;
    right: 2%;
    width: 80px;
    height: 80px;
  }
}
@media screen and (max-width: 1200px) {
  .testimonials .swiper {
    padding: 100px 20px;
  }
  .testimonials .swiper-button-prev {
    top: auto;
    bottom: 0%;
    left: 50%;
    transform: translateX(-120%);
    width: 80px;
    height: 80px;
    background-color: #f1f1f1;
  }
  .testimonials .swiper-pagination {
    display: none;
  }
  .testimonials .swiper-button-next {
    top: auto;
    bottom: 0%;
    right: 50%;
    transform: translateX(120%);
    width: 80px;
    height: 80px;
    background-color: #f1f1f1;
  }
}
