.chart-svg {
  overflow: visible;
}
.chart-svg path {
  transition: 0.3s all;
  opacity: 1;
}
.chart-svg path:hover {
  filter: drop-shadow(1px 1px 8px rgba(0, 0, 0, 0.8));
}
.svg-number {
  font-family: "TT-Hoves-Pro";
  font-weight: 600;
}
.svg-text {
  font-family: "Inter", serif;
}

.chart-container-one svg {
  transform: scale(0.7);
  margin-right: -25px;
  margin-top: -40px;
}

@media screen and (max-width: 1168px) {
  .chart-container-one svg {
    margin-top: -30px;
  }
}
@media screen and (max-width: 1024px) {
  .chart-container-one svg {
    margin-top: -10px;
  }
}

@media screen and (max-width: 768px) {
  .chart-container-one svg {
    transform: scale(0.6);
    margin-right: 0;
    margin-top: -35px;
  }
}
