.board-of-directors {
  text-align: center;
}
.board-of-directors .container {
  max-width: 900px;
}
.directors-grid-wrapper {
  display: grid;
  grid-template-columns: repeat(3, 1fr);

  gap: 40px;
  padding: 20px 0;
}

.director-card {
  text-align: center;
}

.director-card img {
  width: 240px;
}
@media screen and (max-width: 820px) {
  .directors-grid-wrapper {
    grid-template-columns: repeat(2, 1fr);
  }
}
