.finances {
  h2,
  h3 {
    text-align: center;
  }
  h4 {
    margin: 0 0 20px 30px;
  }
  span.number-one {
    font-size: 4.375rem;
    font-family: "TT-Hoves-Pro";
    font-weight: 600;
  }
}
.finances .finances-content-wrapper {
  display: flex;

  justify-content: center;
  gap: 40px;
  padding: 0 20px;
}
.finances .finance-card {
  width: 50%;
}
.finance-card-content-wrapper {
  border-radius: 20px;
  height: 550px;
  background: url("../../assets/images/finances-card-bg.png") no-repeat center
    center / cover;
}
.finances-card-header {
  padding: 25px 35px;
  border-radius: 20px 20px 0 0;
  display: flex;
  align-items: center;
  justify-content: space-between;

  img {
    width: 60px;
  }
}
.finance-card-chart-descr {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-top: 60px;
  ul {
    list-style-type: none;
    font-family: "Inter", serif;
    font-size: 18px;
    margin: 0;
    padding: 0;
    line-height: 1.2em;
  }
  li {
    padding: 2px 0;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  .percentage {
    padding-left: 20px;
    width: 100px;
  }
}
@media screen and (max-width: 768px) {
  .finances .finances-content-wrapper {
    flex-wrap: wrap;
  }
  .finances .finance-card {
    width: 100%;
  }
}
